<template>
    <div class="left-column">
        <div class="review">
            <div>
                <span>Email</span>
                <span>{{ email }}</span>
                <span @click="$emit('previous-step')">Change</span>
            </div>
            <div>
                <span>Ship to</span>
                <span>{{ address }}</span>
                <span @click="$emit('previous-step')">Change</span>
            </div>
            <div>
                <span>Ship by</span>
                <span>{{ method.name }}</span>
                <span @click="$emit('previous-step')">Change</span>
            </div>
        </div>
        <h2 class="payment">Payment</h2>
        <p>All payments are 3D secure encrypted - your payment details are kept secure and safe.</p>
        
        <form action="/charge" method="post" id="payment-form" @submit.prevent="onSubmit($event)">
            <div class="form-row">
                <label for="card-element">
                    Credit or debit card
                </label>
            <div id="card-element">
                <!-- A Stripe Element will be inserted here. -->
                </div>
                
                <!-- Used to display form errors. -->
                <div id="card-errors" role="alert"></div>
            </div>
            <div class="choices">
                <p @click="$router.back()">< Continue shopping</p>
                <button value="submit" class="button button--oldskool">Pay now</button>
            </div>
        
        </form>
        
        
    </div>
</template>

<script>
    export default {
        name: "shipping",
        props: ["email", "address", "method"],
        data() {
            return {
                card: null
            }
        },
        mounted() {
            this.$loadScript("https://js.stripe.com/v3/")
            .then(() => {
                // Create a Stripe client.
                window.stripe = Stripe('pk_test_9yxW3tIbgMojGmrFlt66m1ut00VurR8txa');

                // Create an instance of Elements.
                var elements = window.stripe.elements();
                
                // Custom styling can be passed to options when creating an Element.
                // (Note that this demo uses a wider set of styles than the guide below.)
                var style = {
                  base: {
                    color: '#32325d',
                    fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
                    fontSmoothing: 'antialiased',
                    fontSize: '16px',
                    '::placeholder': {
                      color: '#aab7c4'
                    }
                  },
                  invalid: {
                    color: '#d04949',
                    iconColor: '#d04949'
                  }
                };
                
                // Create an instance of the card Element.
                this.card = elements.create('card', {style: style});
                
                this.card.addEventListener('change', function(event) {
                    var displayError = document.getElementById('card-errors');
                    if (event.error) {
                        displayError.textContent = event.error.message;
                    } else {
                        displayError.textContent = '';
                    }
                });
                
                // Add an instance of the card Element into the `card-element` <div>.
                this.card.mount('#card-element');
                })
                .catch((err) => {
                    console.log(err)
                });
        },
        methods: {
            onSubmit(event) {
                window.stripe.createToken(this.card).then(function(result) {
                    if (result.error) {
                        // Inform the customer that there was an error.
                        var errorElement = document.getElementById('card-errors');
                        errorElement.textContent = result.error.message;
                    } else {
                        // Send the token to your server.
                        this.stripeTokenHandler(result.token);
                    }
                });
            },
            stripeTokenHandler(token) {
                // Insert the token ID into the form so it gets submitted to the server
                const form = document.getElementById('payment-form');
                const hiddenInput = document.createElement('input');
                hiddenInput.setAttribute('type', 'hidden');
                hiddenInput.setAttribute('name', 'stripeToken');
                hiddenInput.setAttribute('value', token.id);
                form.appendChild(hiddenInput);
                
                // Submit the form
                form.submit();
            }
        }
    }
</script>

